import { _decorator, Component, instantiate, Node, Prefab } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('RecipesList')
export class RecipesList extends Component {
    @property(Node)
    content: Node = null;

    @property(Prefab)
    recipeItemPrefab: Prefab = null;

    updateRecipeList(recipes: Array<any>) {
        // 清空列表
        this.content.removeAllChildren();

        recipes.forEach(recipe => {
            const item = instantiate(this.recipeItemPrefab);
            const label = item.getComponent(Label);
            label.string = recipe.name;
            this.content.addChild(item);
        });
    }
}


